* {
  margin: 0;
  padding: 0;
}

.search-form {
  width: 100%;
  height: 90px;
  background: -webkit-linear-gradient(left, #afd496 0%, #a0c67a 71%, #a0c67a 71%, #a0c67a 84%);
  border: 3px solid #000;
  align-items: center;
  display: flex;
}

.search-form input {
  height: 50px;
  width: 250px;
  border: 3px solid red;
  border-radius: 40px;
  margin: 0 50px 0 100px;
  padding: 0 30px;
}

.gallery {
  grid-gap: 35px;
  grid-template-columns: repeat(auto-fill, minmax(230px, 1fr));
  margin: 50px 15px;
  display: grid;
}

.photo-card {
  height: 300px;
  border: 2px solid orange;
  grid-template-rows: max-content 200px 1fr;
  display: grid;
}

.photo-card img {
  object-fit: fill;
  width: 100%;
  height: 100%;
}

